.block {
  width: 100vm;
  height: 720px;
  background: linear-gradient(180deg, #E6D4DE 0%, #9890C7 100%);
  display: flex;
  align-items: center;
  justify-content: center;

  .container {
    width: 412px;
    height: 540px;

    background: #F9F9FE;
    border-radius: 8px;

    display: flex;
    flex-direction: column;
    align-items: center;
    .incubator {
      font-family: 'Poppins';
      font-style: normal;
      font-weight: 600;
      font-size: 26px;
      line-height: 39px;
      margin-top: 25px;
      /* identical to box height */


      color: #2D2E46;
    }
    .forgot {
      font-family: 'Poppins';
      font-style: normal;
      font-weight: 600;
      font-size: 22px;
      line-height: 33px;
      margin-top: 32px;

      color: #2D2E46;
    }

    .inputContainer {
    margin-top: 40px;
    }
    .email {
      font-family: 'SF UI Display';
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      /* or 150% */
      color: #2D2E46;

      opacity: 0.5;
      margin-top: 30px;
      margin-left: 33px;
      text-align: left;
    }
    .buttonContainer {
      margin-top: 99px;
      display: flex;
      flex-direction: column;
      align-items: center;

      .remember {

        font-family: 'SF UI Display';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        margin-top: 30px;
        /* identical to box height, or 150% */

        color: #2D2E46;

        opacity: 0.5;
      }
      .tryLogIn {
        /*position: absolute;*/
        width: 98px;
        height: 24px;
        left: 591px;
        top: 569px;

        font-family: 'SF UI Display';
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        margin-top: 11px;
        /* identical to box height, or 150% */

        text-align: center;

        color: #21268F;
      }
    }
  }
}


